import React from 'react';
import { Link } from 'react-router-dom';

const QuickSearchDisp = (props) => {

    const renderQuickMeal = ({mealtype}) => {
        if(mealtype) {
            return mealtype.map((item) => {
                return (
                    <Link to={`/list/${item.mealtype}`} key={item._id} style={{color:'black'}}>
                        <div className="tiles">
                            <div className="tilecontainer col-md-4 col-sm-4">
                                <div className="tileComponent1">
                                    <img src={`/img/${item.name}.png`} alt="breakfast food image" className="img-responsive" />
                                </div>
                                <div className="tileComponent2 ">
                                    <p className="componentHeading">
                                        {item.name}
                                    </p>
                                    <p className="componentSubHeading ">
                                        {`Start Your day with exclusive ${item.name} options.`}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </Link>
                )
            })
        }
        else{
            return(
                <img src="/img/quicksearchloding.gif" className="lodingImghome img-responsive"/>
            )
        }
    }
    return(

    <div className="quickSearch" >
        <p className="quickSearchHeading">
            Quick Searches
    </p>
        <p className="subHeading">Discover restuarant by type of meal</p>
        {renderQuickMeal(props)}
    </div>)
}

export default QuickSearchDisp;